@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}
@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition: all 300ms;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: $colorTexto;
        color: $bgColor;
    }
}
@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin heading($salto: false) {
    font-weight: 400;

    span {
        font-weight: 700;

        @if($salto) {
            display: block;
        }
    }
    &::after {
        content: '';
        display: block;
        width: 15rem;
        height: 1rem;
        margin-top: 1rem;

        background-image: 
        linear-gradient(
            to right, 
            v.$primario 0%, 
            v.$primario 50%, 
            lighten(v.$primario, 20%) 50%, 
            lighten(v.$primario, 20%) 100%
        );
    }
}