@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-image: linear-gradient(to right, transparentize(v.$primario, .1) 0%, transparentize(v.$primario, .1) 100%), 
    url(../img/header_bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 3rem 0;
    position: relative;

    @include m.tablet{
        padding: 3rem 0 20rem 0;
    }
    &__barra {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__logo {
        width: 25rem;
    }
    &__contenido {
        @include m.tablet{
            margin-top: 10rem;
            max-width: 50rem;
        }
    }
    &__heading {
        text-align: center;
        color: v.$blanco;

        @include m.tablet{
            text-align: left;
        }
    }
    &__texto {
        color: v.$blanco;
        display: none;

        @include m.tablet{
            display: block;
        }
    }
    &__img {
        display: none;

        @include m.tablet{
            display: block;
            position: absolute;
            right: 0;
            bottom: -5rem;
            max-width: 35rem;
            border-radius: 2rem 0 0 5rem;
        }
        @include m.desktopXL{
            max-width: 60rem;
        }
    }
}
.nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;

    @include m.tablet {
        margin-top: 0;
        flex-direction: row;
        gap: 2rem;
    }
    &__link {
        margin-bottom: 2rem;
        font-size: 1.8rem;
        color: v.$blanco;

        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet{
            margin-bottom: 0;
        }
    }
}